.reco-parent-con{
    width: 100%;
    max-width: 600px;
    margin: auto;   
}
.reco-h-title{
    text-align: center;
    margin-left: 8px;
    color: var(--primary-light-b);
    margin-bottom: 0px;
    margin-top: 16px;
    margin-left: auto;
    margin-right: auto;
}

.dark .reco-h-title{
    color: var(--primary-light);
}

.reco-sub-title{
    margin-top: 0px;
    color: #555;
    font-size: 14px;
    font-weight: 600;
}

.reco-list-con{
    display: block;
    padding: 8px;
}

.ri-bg{
    background-color: rgba(0,0,0,0.05);
}

.dark 
.ri-bg{
    background-color: rgba(255,255,255,0.02);
    /* background-color: rgba(255,255,255,0); */
}

.reco-item{
    display: flex;
    flex-direction: row;
    padding: 12px;
    /* border: solid 1px black; */
    margin-bottom: 4px;
    border-radius: 8px;
    
}

.reco-item-cover-con{
    position: relative;
    width: 100px;
    height: 136px;
    background-color: #555;
    text-decoration: none;
}

.reco-item-cover-con img{
    width: 100px;
    height: 136px;
    cursor: pointer;
    object-fit: cover;
}

.reco-item-cover-con .reco-item-badge-con{
    position: absolute;
    top: 2px;
    left: 0px;
    display: flex;
    align-items: center;
    flex-direction: row;
}

.reco-item-summary{
    flex: 1;
    text-align: left;
    margin-left: 16px;
    overflow: hidden;
}

.reco-badge-adult{
    font-size: 10px;
    font-weight: 600;
    background-color: rgb(240, 49, 49);
    color: #FFF;
    padding: 2px 4px;
    margin-left: 2px;
    border-radius: 4px;
}

.reco-badge-new{
    font-size: 10px;
    font-weight: 600;
    background-color: rgb(60, 125, 247);
    color: #FFF;
    padding: 2px 4px;
    margin-left: 2px;
    border-radius: 4px;
}

.ris-title{
    font-weight: 600;
    font-size: 14px;
    white-space: nowrap;
    text-overflow: ellipsis;
    display: block;
    overflow: hidden;
    margin-bottom: 16px;
    cursor: pointer;
    text-decoration: none;
}

.ris-itm-con{
    display: flex;
    flex-direction: column;
    align-items: baseline;
}

.dark
.ris-title{
    color: var(--primary-light);
}

.ris-rating{
    font-size: 12px;
    margin-bottom: 16px;
}

.ris-chap-item-con{
    display: flex;
    align-items: center;
    flex-direction: row;
    margin-bottom: 8px;
    text-decoration: none;
}

.ris-chap-item{
    background-color: rgba(0,0,0,0.1);
    font-size: 12px;
    border-radius: 12px;
    padding: 8px 12px;
    font-weight: 600;
    color: #555;
}

.risci-post-on{
    font-size: 10px;
    margin-left: 8px;
    color: #555;
}

.dark
.risci-post-on{
    color: #888;
}

.dark
.ris-rating{
    color: #888;
}

.dark
.ris-chap-item{
    
    background-color: rgba(255,255,255,0.4);
    color: #222;
}

.ris-btn-con{
    margin-top: 16px;
    margin-bottom: 24px;
    display: flex;
    align-items: center;
    flex-direction: row;
    padding: 8px;
    overflow: hidden;
}

.ris-btn-con 
button{
    background-color: rgba(0,0,0,0);
    height: 40px;
    flex: 1;
    border: solid #888 1px;
    border-radius: 8px;
    font-size: 14px;
    color: #333;
    font-weight: 300;
    cursor: pointer;
    outline: none;
    box-sizing: border-box;
}

.ris-btn-con 
button:hover{
    background-color: rgba(0,0,0,0.1);
}

.ris-btn-con 
button:active{
    background-color: rgba(0,0,0,0.3);
}

.ris-btn-con 
button:disabled{
    opacity: 0.3;
}

.dark
.ris-btn-con 
button{
    color: #fff;
    border: solid #555 1px;
}

.risci-new{
    background-color: rgb(240, 49, 49);
    color: #FFF;
    padding: 4px 8px;
    border-radius: 4px;
    font-weight: 600;
    font-size: 8px;
}

.curr-page{
    text-align: left;
    margin-left: 8px;
    margin-right: 8px;
    color: #555;
    border-bottom: solid #555 1px;
    padding-bottom: 8px;
    margin-bottom: 8px;
}

.sel-reco-pg{
    flex: 2;
    background-color: rgba(0,0,0,0);
    height: 40px;
    border-radius: 8px;
    border: solid #888 1px;
    font-size: 14px;
    outline: none;
    color: #555;
    overflow: hidden;
    box-sizing: border-box;
    width: 100%;
    padding: 0px 8px;
}

.dark
.sel-reco-pg{
    border: solid #555 1px;
    
}

.cat-btn-con{
    display: flex;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 4px;
    justify-content: center;
}

.cat-btn{
    border-radius: 8px;
    border: solid #555 1px;
    color: #555;
    margin: 4px;
    padding: 8px;
    background-color: rgba(0,0,0,0);
    font-size: 14px;
    box-sizing: border-box;
    cursor: pointer;
}

.dark
.cat-btn{
    border: solid var(--primary-light) 1px;
    color: var(--primary-light);
    
}

.cat-act{
    border: solid #333333 1px;
    color: #FFF !important;
    /* font-weight: 600; */
    background-color: #333;
}

.dark
.cat-act{
    border: solid var(--primary-light) 1px;
    color: #222 !important;
    /* font-weight: 600; */
    background-color: var(--primary-light);
}

.reco-tab-con{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.reco-tab-splitter{
    width: 1px;
    background-color: rgba(0,0,0,0.1);
    height: 40px;
    /* margin-left: 1px;
    margin-right: 1px; */
}

.dark
.reco-tab-splitter{
    background-color: rgba(255,255,255,0.1);
}

.reco-tab-con button{
    background-color: rgba(0,0,0,0);
    border: none;
    border-bottom: solid 3px rgba(0,0,0,0.3);
    box-sizing: border-box;
    flex: 1;
    height: 56px;
    color: #333;
    font-weight: 600;
    cursor: pointer;
    outline: none;
}

.reco-tab-con
.rtb-act{
    border-bottom: solid 3px var(--primary-light-b);
    color: var(--primary-light-b);
}

.dark
.reco-tab-con 
button{
    color: #888;
    border-bottom: solid 3px rgba(255,255,255,0.3);
}

.dark
.reco-tab-con
.rtb-act{
    border-bottom: solid 3px var(--primary-light);
    color: var(--primary-light);
}