.snackbar-con{
    position: fixed;
    /* bottom: 16px; */
    left: 16px;
    right: 16px;
    padding: 16px 16px;
    background-color: var(--primary);
    display: flex;
    align-items: center;
    flex-direction: row;
    max-width: 400px;
    margin: auto;
    border-radius: 8px;
    box-shadow: 0px 4px 12px rgba(0,0,0,0.5);
    overflow-x: hidden;
    box-sizing: border-box;
    transform: translateZ(0);
    z-index: 999;
}

.sbar-open-anim{
    animation: erl-slide-up 0.3s both cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.sbar-close-anim{
    animation: erl-slide-down 0.3s both cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.sbar-open-anim-top{
    animation: erl-slide-up-from-top 0.3s both cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.sbar-close-anim-top{
    animation: erl-slide-down-from-top 0.3s both cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.sbar-btn-ico{
    color: #FFF;
    margin-right: 16px;
    font-weight: 300;
}

.sbar-message{
    flex: 1;
    font-size: 14px;
    color: #FFF;
    margin-right: 40px;
    box-sizing: border-box;
    overflow: hidden;
}

.sbar-btn-x{
    height: 28px;
    width: 28px;
    position: absolute;
    right: 16px;
    background-color: rgba(0,0,0,0);
    color: #FFF;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 50%;
    font-size: 20px;
    outline: none;
    cursor: pointer;
}

.sbar-btn-x:hover{
    background-color: rgba(0,0,0,0.05);
}

.sbar-btn-x:hover{
    background-color: rgba(0,0,0,0.1);
}

@keyframes erl-slide-up{
    from{
        opacity: 0;
        transform: translateY(100%);
    }


    to{
        opacity: 1;
        transform: translateY(0%);
    }
}

@keyframes erl-slide-up-from-top{
    from{
        opacity: 0;
        transform: translateY(-100%);
    }


    to{
        opacity: 1;
        transform: translateY(0%);
    }
}

@keyframes erl-slide-down{
    from{
        opacity: 1;
        transform: translateY(0%);
    }


    to{
        opacity: 0;
        transform: translateY(100%);
    }
}

@keyframes erl-slide-down-from-top{
    from{
        opacity: 1;
        transform: translateY(0%);
    }


    to{
        opacity: 0;
        transform: translateY(-100%);
    }
}