.sf-con{
    position: fixed;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    background-color: rgba(0,0,0,0.3);
    z-index: 2000;
    padding: 16px;
    overflow-x: hidden;
    overflow-y: auto;

    display: flex;
    align-items: center;
}

.sf-card{
    background-color: var(--primary-dark);
    width: 100%;
    box-sizing: border-box;
    min-height: 100px;
    border-radius: 4px;
    box-shadow: 0px 2px 12px rgba(0,0,0,0.5);
    display: flex;
    flex-direction: column;
    max-width: 540px;
    margin: auto;
    animation: zoom-in 0.3s both;
}

.sf-head{
    display: flex;
    align-items: center;
    height: 56px;
    border-bottom: solid 1px rgba(0,0,0,0.1);
    width: 100%;
    box-sizing: border-box;
}

.sf-content{
    padding: 16px;
}

.sf-foot{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    /* height: 56px; */
    border-top: solid 1px rgba(0,0,0,0.1);
    width: 100%;
    box-sizing: border-box;
    padding: 16px;
}

.sf-head-title{
    padding-left: 16px;
    font-weight: 600;
    font-size: 16px;
    color: var(--primary-light);
}

.sf-in-con{
    display: flex;
    width: 100%;

}

.sf-in-con input{
    flex: 1;
    padding: 16px;
    font-size: 16px;
    border: solid 0px rgba(0,0,0,0.1);
    border-radius: 4px;
    background-color: rgba(0,0,0,0.3);
    color: #FFF;
}

.btn-save{
    border: none;
    background-color: var(--primary-light);
    padding: 8px 16px;
    font-weight: 600;
    color: var(--primary-dark);
    border-radius: 4px;
}

.btn-close{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0,0,0,0);
    border: none;
    height: 40px;
    width: 40px;
    margin: 8px;
}