.recent-item-con{
    display: flex;
    flex-direction: row;
    border-radius: 4px;
    background-color: rgba(0,0,0,0.08);
    margin-bottom: 4px;
    text-decoration: none;
    color: #333;
    overflow: hidden;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    padding: 0px;
}

.dark
.recent-item-con{
    background-color: rgba(255,255,255,0.02);
}

.ritm-cover-con{
    width: 100px;
    height: 160px;
    background-color: rgba(0,0,0,0.2);
}

.ritm-cover-con img{
    width: 100px;
    height: 160px;
    object-fit: cover;
}

.ritm-info-con{
    flex: 1;
    text-align: left;
    padding: 16px;
    display: flex;
    flex-direction: column;
}

.ritm-title{
    font-size: 16px;
    font-weight: 600;
    color: rgb(38, 97, 187);
}

.dark
.ritm-title{
    color: var(--primary-light);
}

.ritm-chap{
    font-size: 14px;
}

.dark
.ritm-chap{
    color: #FFF;
}

.ritm-date{
    font-size: 12px;
    margin-top: 16px;
    /* text-align: right; */
}
.ritm-time{
    font-size: 12px;
    /* text-align: right; */
}

.dark
.ritm-date,
.dark
.ritm-time{
    color: #888;
}

.recent-pg-footer{
    border-top: solid 1px rgba(255,255,255,0.2);
    height: 56px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 8px;
    padding-right: 8px;
}