
.logo-loading-con{
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
}

.logo-loading-con img{
    max-width: 120px;
    width: 100%;
    transform: translateZ(0px);
    animation: logo-jump 0.8s both infinite linear;
    transform-origin: bottom;
}

@keyframes logo-jump{
    0%{
        transform: scale3d(1,1,1) translateY(0px);
    }

    25%{
        transform: translateY(-20px);
    }

    50%{
        transform: translateY(-24px);
    }

    70%{
        transform: scale3d(1,1,1);
    }

    80%{
        transform: scale3d(1,0.9,1) translateY(0px);
    }

    100%{
        transform: scale3d(1,1,1) translateY(0px);
    }
}