.dl-modal-con{
    position: fixed;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    background-color: var(--dl--con-bg-color);
    display: flex;
    flex-direction: column;
}

.dl-header{
    min-height: 56px;
    box-sizing: border-box;
    width: 100%;
    border-bottom: solid 1px var(--border-color);
    padding: 16px;
    color: var(--text-color);
    font-size: 24px;
    font-weight: 300;
    text-align: center;
}

.dl-content{
    flex: 1;
    padding: 16px;
    color: var(--text-color);
    overflow-x: hidden;
    overflow-y: auto;
}

.dl-content img{
    margin-left: auto;
    margin-right: auto;
    display: block;
}

.dl-content img[data-role="img-cover"]{
    margin-left: auto;
    margin-right: auto;
    display: block;
    /* border: solid 1px #FFF; */
    border-radius: 8px;
    box-shadow: 0px 2px 2px rgba(0,0,0,0.3);
}

.dl-progbar-con{
    margin-top: 8px;
    margin-bottom: 8px;
    height: 40px;
    position: relative;
    box-sizing: border-box;
    width: 100%;
    border: solid 0px var(--border-color);
    overflow: hidden;
    border-radius: 8px;
    background-color: #777;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}

.dl-progbar-title{
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #FFF;
}

.dl-progbar{
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    /* width: 50%; */
    background-color: rgb(45, 153, 45);
}

.dl-footer{
    padding: 16px;
    border-top: solid 1px var(--border-color);
    display: flex;
    align-items: center;
    flex-direction: row;
}

.btnDLStop{
    flex: 1;
    height: 40px;
    color: #FFF;
    background-color: rgb(216, 47, 47);
    border: none;
    border-radius: 8px;
    font-size: 16px;
    outline: none;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}