.siwt-list-con{
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 600px;
}

.siwt-con{
    display: flex;
    align-items: center;
    flex-direction: row;
    border-bottom: solid 1px var(--border-color);
    padding-top: 16px;
    padding-bottom: 16px;
}

.siwt-lbl{
    flex: 1;
    text-align: left;
    padding-right: 16px;
    font-size: 14px;
}

.siwt-lbl span{
    font-size: 12px;
    opacity: 0.5;
}

.sett-page h4{
    text-align: left;
    color: var(--text-color);
    margin-bottom: 0px;
    border-bottom: solid 1px var(--border-color);
    padding-bottom: 16px;
    font-weight: 600;
    font-size: 16px;
}

.siwt-btn{
    border: none;
    background-color: #555;
    color: #FFF;
    min-height: 40px;
    padding: 8px 16px;
    display: flex;
    align-items: center;
    border-radius: 8px;
    cursor: pointer;
    outline: none;
}

.siwt-con .del-color{
    background-color: rgb(218, 48, 48);
}

.siwt-con .del-color:hover{
    background-color: rgb(228, 81, 81);
}

.siwt-con .del-color:active{
    background-color: rgb(169, 39, 39);
}