.sp2-con{
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    overflow: hidden;
    background-color: #333;
    -webkit-overflow-scrolling: touch;
}

.sp2-nav{
    height: 56px;
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    height: 56px;
    /* background-color: rgba(0,0,0,0.2); */
    display: flex;
    align-items: center;
    flex-direction: row;
}

.sp2-cover-holder{
    /* height: 360px; */
    background-color: #333;
    position: relative;
    display: flex;
    align-items: center;
    overflow: hidden;
    box-sizing: border-box;
    height: 100%;
    
}

.sp2-cp-con{
    display: flex;
    align-items: center;
    max-width: 600px;
    position: absolute;
    top: 0px;
    left: 0px;
    /* bottom: 0px; */
    right: 0px;
    margin-left: auto;
    margin-right: auto;
    height: 360px;
    perspective: 300px;
}

.sp2-cover-pic{
    position: absolute;
    right: 24px;
    height: 256px;
    border-radius: 16px;
    transform: rotateY(-15deg);
    /* transform-style: preserve-3d; */
    transform-origin: right;
    /* border: solid 2px #555; */
    
    /* margin-top: auto; */
    animation: cover-anim 4s both alternate-reverse infinite;
}

.sp2-cover-bg{
    position: absolute;
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    object-fit: cover;
    opacity: 0.3;
    filter: blur(4px);
    /* margin: -16px; */
}

.sp2-content-holder{
    position: absolute;
    top: 56px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    overflow-x: hidden;
    overflow-y: auto;
    /* background-color: red; */
}

.sp2-cover-window{
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    /* height: 272px; */
    /* height: 200px; */
    /* display: flex;
    flex-direction: column; */
    text-align: left;
    /* justify-content: center; */
    padding: 0px 20px;
    /* box-sizing: border-box; */
    overflow-x: hidden;
    overflow-y: auto;
    padding-bottom: 16px;
    box-sizing: border-box;
}

.sp2-title{
    margin-top: 16px;
    font-size: 24px;
    color: #FFF;
    padding-right: 96px;
    font-weight: 100;
    /* text-shadow: 0px 2px rgba(0,0,0,0.3); */
    /* font-family: sans-serif; */
}

.sp2-src-site{
    color: #CCC;
    font-size: 12px;
    margin-top: 4px;
}

.sp2-src-site a{
    color: #FFF;
    margin-left: 4px;
}

.btn-re1{

    margin-top: 16px;
    padding: 8px 16px;
    box-sizing: border-box;
    background-color: rgba(0,0,0,0);
    border-radius: 50px;
    border: solid 1px #FFF;
    color: #FFF;
    outline: none;
}

.sp2-content{
    padding: 16px;
    border-radius: 16px 16px 0px 0px;
    display: block;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
    background-color: var(--layout-bg-color);
    /* min-height: 1000px; */
}

