@media screen and (min-width: 601px){
    .sp2-content{
        border-radius: 16px 16px 16px 16px;
        margin-bottom: 16px;
    }
}

.si2-con{
    display: flex;
    align-items: center;
    flex-direction: row;
    /* display: block; */
    font-size: 12px;
    padding: 8px;
    min-height: 56px;
    box-sizing: border-box;
    
    background-color: rgba(0,0,0,0);
    cursor: pointer;
    text-decoration: none;
    position: relative;
    border-bottom: solid 1px var(--border-color);
}

.si2-dl-lbl{
    color: #000;
    margin-right: auto;
    display: inline-block;
    background-color: var(--primary-light);
    font-weight: 600;
    padding: 4px 8px;
    border-radius: 4px;
    margin-top: 12px;
    font-size: 10px;
}

.ci-dl-prog-con{
    position: absolute;
    z-index: 20;
    background-color: rgba(0,0,0,0.5);
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
}

.ci-dl-prog-bar{
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    width: 0%;
    background-color: rgba(0, 51, 255, 0.508);
}

.ci-dl-prog-bar-lbl{
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    z-index: 2;
    color: #FFF;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    font-weight: 600;
}
