    background-color: rgba(0,0,0,0.05);
}

.si2-con:active{
    background-color: rgba(0,0,0,0.1);
}

.si2-title{
    flex: 1;
    text-align: left;
    font-size: 14px;
    font-weight: 600;
    color: var(--text-color);
}

