.si2-date{
    font-size: 12px;
    color: #777;
    text-align: left;
    margin-top: 2px;
}

.sp2-desc{
    font-size: 12px;
    margin-top: 16px;
    display: block;
    padding-right: 100px;
    color: #EEE;
}

.sp2-item-number{
    margin-right: 4px;
    margin-left: 8px;
    height: 100%;
    /* background-color: #333; */
    padding: 8px;
    border-radius: 8px;
    color: #777;
    border: solid 1px #aaa;
}


.dark
.sp2-item-number{
    color: #999;
    border: solid 1px rgba(255,255,255,0.1);
}

.sp2-top-btns{
    display: flex;
    align-items: center;
    flex-direction: row;
}

.sp2-top-btns button{
    display: flex;
    align-items: center;
    margin-right: 8px;
    font-size: 12px;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0);
    transition: all 0.2s;
}

.sp2-top-btns button:hover{
    background-color: rgba(0, 0, 0, 0.1);
}

.sp2-top-btns button:active{
    background-color: rgba(0, 0, 0, 0.2);
}

.pop-add-to-list{
    position: absolute;
    top: 50px;
    /* right: 39px; */
    background-color: #FFF;
    padding: 8px 16px;
    font-size: 12px;
    font-weight: 600;
    border-radius: 16px;
    animation: blink 2s 5 both;
    pointer-events: none;
    /* position: relative; */
    min-width: 68px;
    z-index: 999;
}

.pop-add-to-list::after{
    content: '';
    border-bottom: 8px solid #fff;
    border-top: 8px solid rgba(0, 0, 0, 0);
    border-left: 8px solid rgba(0, 0, 0, 0);
    border-right: 8px solid rgba(0, 0, 0, 0);
    position: absolute;
    top: -16px;
    margin-left: auto;
    margin-right: auto;
    /* background-color: #FFF; */
    height: 0px;
    width: 0px;
    z-index: 2;
    left: 0px;
    right: 0px;
}

.dark
.sp2-content
h3{
    color: var(--primary-light);
}


.dark
.sp2-nav
.hp2-h-btn
[data-fillable]{
    fill: #ddd;
}

.hp2-h-btn:disabled{
    opacity: 0.3;
}

@media screen and (max-width: 400px){
    .sp2-top-btns{
        display: block;
    }
}


@keyframes cover-anim {
    from{
      /* opacity: 1; */
      transform: scale3d(1,1,1) rotateY(-20deg) rotateX(0deg) translateY(0px);
    }

    to{
      /* opacity: 0.5; */
      transform: scale3d(0.9,0.9,0.9) rotateY(-15deg) rotateX(-8deg) translateY(10px);
    }
  }

  @keyframes blink{
    0%{
        opacity: 0;
    }

    25%{
        opacity: 1;
    }

    75%{
        opacity: 1;
    }

    100%{
        opacity: 0;
    }
  }
