.cp2-con{
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    /* overflow: hidden; */
}

.dark
.cp2-con{
    background-color: var(--primary-dark);
}

.dark
.cp2-con
[data-fillable]{
    fill: #ddd;
}


.cp2-content{
    /* height: 2000px; */
    padding-top: 56px;
    padding-bottom: 56px;

}

.cp2-content img{
    display: block;
    box-sizing: border-box;
    width: 100%;
    max-width: 720px;
    margin: auto;
}

.cp2-head{
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    height: 56px;
    background-color: #333;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    flex-direction: row;
    transition: all 0.2s;
    transform: translateY(0px);
}

.cp2-title{
    color: #FFF;
    flex: 1;
    overflow: hidden;
    text-align: left;
    text-overflow: ellipsis;
}

.cp2-foot{
    position: fixed;
    bottom: 0px;
    left: 0px;
    right: 0px;
    height: 56px;
    background-color: #333;
    display: flex;
    align-items: center;
    flex-direction: row;
    transition: all 0.2s;
    transform: translateY(0px); 
    overflow: visible;
}

.cp2-story-index-lbl{
    color: #FFF;
}

.foot-over{
    position: absolute;
    top: -48px;
    left: 0px;
    right: 0px;
    background-color: rgba(0,0,0,0.3);
    width: 72px;
}

.dark
.foot-over
[data-fillable]{
    fill: #fff;
}


.fo-cen{
    margin-left: auto;
    margin-right: auto;
}

.fo-left{
    margin-right: auto;
}

.fo-right{
    margin-left: auto;
}
