.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

@font-face {
  font-family: roboto;
  -webkit-font-smoothing: antialiased;
  src:  url(./assets/fonts/Roboto-Regular.ttf);
}

@font-face {
  font-family: elixia;
  -webkit-font-smoothing: antialiased;
  /* src: url(./Fonts/MaterialIcons-Regular.ttf);  */
  src:  url(./assets/fonts/elixia.ttf) format('truetype');
}


@font-face {
  font-family: m-icon;
  -webkit-font-smoothing: antialiased;
  /* src: url(./Fonts/MaterialIcons-Regular.ttf);  */
  src:  url(./assets/fonts/MaterialIcons-Regular.eot);

  src:  url(./assets/fonts/MaterialIcons-Regular.woff2) format('woff2'),
        url(./assets/fonts/MaterialIcons-Regular.woff) format('woff'),
        url(./assets/fonts/MaterialIcons-Regular.ttf) format('truetype');
  font-feature-settings: 'liga';
}


@font-face {
  font-family: fa;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  src:  url(./assets/fonts/fa-regular.ttf) format('truetype');
}

@font-face {
  font-family: fa;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  src:  url(./assets/fonts/fa-light-300.ttf) format('truetype');
}

@font-face {
  font-family: fa;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  src:  url(./assets/fonts/fa-brand-400.ttf) format('truetype');
}

@font-face {
  font-family: fa;
  font-weight: 900;
  -webkit-font-smoothing: antialiased;
  src:  url(./assets/fonts/fa-solid-900.ttf) format('truetype');
}


body, div, h1, h2, h3, h4, h5, h6, button, input, textarea{
  font-family: roboto, Arial, Helvetica, sans-serif;
}

body{
  margin: 0px;
  padding: 0px;
  background-color: #FFF;
  height: 100vh;
  width: 100vw;
  box-sizing: border-box;
  position: relative;
}




:root{
  --primary-dark: #222222;
  --primary-light: #f3c669;
  --primary-light-b: rgb(30, 89, 226);

}


.dark{
  --text-color: #f3c669;
  --title-color: #f3c669;
  --layout-bg-color: #222222;
  --border-color: rgba(255,255,255,0.1);
  --dl--con-bg-color: #222;
}

.light{
  --text-color: #1c578f;
  --layout-bg-color: #f3f3f3;
  --title-color: #222;
  --border-color: rgba(0,0,0,0.15);
  --dl--con-bg-color: #f3f3f3;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* #root img{
  display: block;
  width: 100%;
  max-width: 720px;
  margin: auto;
} */

.is-loading{
  display: block;
  margin-left: auto;
  margin-right: auto;
}


.app-bar{
  height: 72px;
  background-color: rgba(0,0,0,0);
  color: var(--primary-light);
  font-size: 24px;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 16px;
  box-sizing: border-box;
}

.app-bar input{
  background-color: rgba(255,255,255,0.1);
  border: none;
  height: 40px;
  padding: 0px 16px;
  box-sizing: border-box;
  width: 100%;
  outline: none;
  border-radius: 8px;
  font-size: 16px;
  color: #DDD;
  max-width: 600px;
}

.m-icon{
  font-size: 24px;
  font-family: m-icon;
}

.dark{
  background-color: #333 !important;
}

.dark
#root{
  background-color: #333;
}

.dark
[data-fillable]{
  fill: #999;
}


.light
.h-link{
  color: #3f5bdb;
}

.dark
.h-link{
  color: var(--primary-light);
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
