.toggle-con{
    height: 32px;
    width: 60px;
    background-color: #ccc;
    /* background-color: #4CAF50; */
    border-radius: 20px;
    box-sizing: border-box;
    position: relative;
    cursor: pointer;
    transition: all 0.3s;
    outline: none;
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
}


.light-theme
.toggle-con-bg{
    background-color: #ccc;
}

.dark-theme
.toggle-con-bg{
    background-color: #444;
}

.toggle-circle{
    position: absolute;
    top: 4px;
    left: 4px;
    box-sizing: border-box;
    background-color: #FFF;
    height: 24px;
    width: 24px;
    border-radius: 50%;
    transition: all 0.2s;
}

.toggle-disabled{
    cursor: not-allowed;
    opacity: 0.5;
}
