.drawer-bg{
    position: fixed;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    z-index: 800;
    background-color: rgba(0,0,0,0.7);
    opacity: 0;
    display: none;
}



.drawer-card{
    z-index: 801;
    position: fixed;
    top: 0px;
    left: -256px;
    bottom: 0px;
    width: 256px;
    
    transform: translateX(0px);
    overflow-x: hidden;
    overflow-y: auto;
    padding-left: 16px;
    padding-right: 16px;
    box-sizing: border-box;
    border-radius: 0px 0px 0px 0px;
    background-color: var(--layout-bg-color);
    /* transition: all .016s linear; */
}

.drawer-logo-con{
    padding: 16px;
    height: 120px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.drawer-logo-con img{
    display: block;
    box-sizing: border-box;
    height: 100%;
}

.dbutton{
    box-sizing: border-box;
    height: 56px;
    width: 100%;
    padding: 0px;
    display: flex;
    align-items: center;
    outline: none;
    margin-top: 8px;
    margin-bottom: 8px;
    border-radius: 8px;
    cursor: pointer;
}

.dbutton:hover{
    background-color: rgba(0,0,0,0.05);
}

.dbutton:active{
    background-color: rgba(0,0,0,0.1);
}

.light
.dbutton{
    border: solid 1px var(--border-color);
    background-color: rgba(0,0,0,0);
    color: #333;
}


.dark
.dbutton{
    border: solid 1px var(--border-color);
    background-color: rgba(0,0,0,0);
    color: #888;
}


.dbutton-ico{
    height: 56px;
    width: 56px;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    /* color: #555; */
}

.db-lbl{
    font-size: 14px;
    font-family: roboto, Arial, Helvetica, sans-serif;
    pointer-events: none;
    
}

.light
.db-act{
    background-color: rgb(61, 132, 226) !important;
    border: solid 1px rgb(61, 132, 226);
    color: #FFF;
}

.dark
.db-act{
    background-color: var(--primary-light) !important;
    border: solid 2px var(--primary-light);
    color: var(--primary-dark);
}



.db-act
.db-lbl{
    font-weight: 600;
}

.darkmode-toggle-holder{
    display: flex;
    align-items: center;
    flex-direction: row;
    border: solid 1px rgba(0,0,0,0.1);
    padding: 16px;
    border-radius: 8px;
    margin-bottom: 8px;
}

.dth-dm-lbl{
    flex: 1;
    color: #333;
    font-size: 14px;
}

.dark
.darkmode-toggle-holder{
    border: solid 1px rgba(255,255,255,0.1);
}


.dark
.dth-dm-lbl{
    color: #777;
}