.sip-con{
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    -webkit-overflow-scrolling: touch;
}

.dark
.sip-con{
    background-color: var(--primary-dark);
}

.sip-title{
    font-size: 32px;
    text-align: center;
    margin-top: 16px;
}   

.dark
.sip-title{
    color: var(--primary-light);
}

.sip-content{
    padding: 16px;
    flex: 1;
    overflow-x: hidden;
    overflow-y: auto;
    box-sizing: border-box;
    display: block;
}

.dark
.sip-content{
    color: #999;
}

.sip-info{
    padding: 8px 0px;
}

.sip-desc{
    font-size: 14px;
    text-align: justify;
    padding: 8px;
}

.sip-info-item-con{
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    font-size: 14px;
    padding: 8px;
}

.sip-ii-key{
    width: 100px;
    text-align: left;
    font-weight: 600;
    word-wrap: break-word;
}

.dark 
.sip-ii-key{
    color: var(--primary-light);
}

.sip-ii-val{
    flex: 1;
    overflow: hidden;
    text-align: left;
    word-wrap: break-word;
}