.lio-con{
    position: fixed;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    /* background-color: rgba(0,0,0,0.5); */
    z-index: 999;
    display: flex;
    align-items: flex-end;
    overflow-x: hidden;
    overflow: hidden;
}

.lio-bg{
    position: fixed;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    z-index: 1000;
    background-color: rgba(0,0,0,0.6);
    transition: all 0.3s;
    transform: translateZ(0px);
}

.lio-card{
    width: 100%;
    box-sizing: border-box;
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 16px 16px 0px 0px;
    z-index: 1001;
    /* animation: slide-up-card 0.3s both; */
    transition: all 0.3s;
    transform: translateZ(0px);
    background-color: var(--layout-bg-color);
}

