.lio-head{
    min-height: 56px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    padding-top: 24px;
    padding-bottom: 24px;

}

.lio-head-title{
    font-weight: 600;
    padding-left: 16px;
    padding-right: 16px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}

.light
.lio-head-title{
    color: #333;
}

.dark
.lio-head-title{
    color: var(--primary-light);
}

.lio-content{
    display: block;
    border-top: solid 1px rgba(0,0,0,0.1);
    padding: 8px 16px;
}

.dark
.lio-content{
    border-top: solid 1px rgba(258,255,255,0.1);
}

.lio-btns button{
    display: flex;
    align-items: center;
    flex-direction: row;
    box-sizing: border-box;
    width: 100%;
    height: 56px;
    padding: 0px;
    border: solid 1px var(--border-color);
    /* border-bottom: solid 2px #DDD; */
    background-color: rgba(0,0,0,0);
    font-size: 14px;
    cursor: pointer;
    outline: none;
    color: #333;
    margin: 8px 0px;
    border-radius: 8px;
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
    /* box-sizing: border-box; */
}


.lio-btns button:hover{
    background-color: rgba(0,0,0,0.05);
    
}

.lio-btns button:active{
    background-color: rgba(0,0,0,0.1);
    
}

.dark
.lio-btns 
button{
    /* border: solid 1px rgba(255,255,255,0.1); */
    color: #777;
}

.lio-btn-ico{
    height: 56px;
    width: 56px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.lio-title{
    display: block;
    text-align: center;
    font-size: 16px;
    margin-bottom: 16px;
    color: var(--primary-light);
    padding: 16px;
}

.lio-h-from{
    font-size: 12px;
    font-weight: 100;
    color: rgba(0,0,0,0.6);
}

.light
.lio-h-from{
    color: rgba(0,0,0,0.6);
}

.dark
.lio-h-from{
    color: rgba(255,255,255,0.3);
}


@keyframes slide-up-card{
    from{
        transform: translateY(100%);
    }

    to{
        transform: translateY(0%);
    }
}