.ap-con{
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.dark 
.ap-con{
    background-color: var(--primary-dark);
    
}

.ap-head{
    height: 56px;
    border-bottom: solid 1px #DDD;
    display: flex;
    align-items: center;
    flex-direction: row;
}

.dark .ap-head{
    border-bottom: solid 1px rgba(255,255,255,0.1);
}

.ap-h-title{
    font-weight: 600;
    font-size: 14px;
    color: #333;
}

.dark
.ap-h-title{
    color: #777;
}


.ap-content{
    flex: 1;
    background-color: #fff;
    overflow-x: hidden;
    overflow-y: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
}

.dark
.ap-content{
    background-color: var(--primary-dark);
}

.ap-foot{
    position: relative;
    height: 56px;
    border-top: solid 1px #DDD;
    display: flex;
    align-items: center;
    flex-direction: row;
    /* padding: 4px; */
    box-sizing: border-box;
}

.dark
.ap-foot{
    border-top: solid 1px rgba(255,255,255,0.1);
}

.ap-foot button{
    flex: 1;
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    border: none;
    /* border: solid 1px #DDD; */
    background-color: rgba(0, 0, 0, 0);
    height: 56px;
    outline: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border-radius: 8px; */
}

.ap-foot button:active{
    background-color: rgba(0, 0, 0, 0.1);
}

.ap-f-btn-lbl{
    font-size: 8px;
    margin-top: 4px;
    color: #555;
}

.ap-tab-act-line{
    position: absolute;
    top: 0px;
    /* width: 50%; */
    box-sizing: border-box;
    height: 2px;
    background-color: rgba(0, 0, 0, 0.5);
    transform: translateZ(0);
    transition: all 0.3s;
    border-radius: 4px;
}


.dark
.ap-tab-act-line{
    background-color: var(--primary-light);
}

.link-frm-con{
    /* position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px; */
    background-color: #FFF;
    display: block;
    /* display: flex;
    flex-direction: column; */
    /* justify-content: center; */
    box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 16px;
    text-align: left;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}

.dark 
.link-frm-con{
    background-color: var(--primary-dark);
}

.link-frm-con input{
    display: block;
    width: 100%;
    height: 40px;
    box-sizing: border-box;
    border-radius: 8px;
    border: none;
    background-color: rgba(0, 0, 0, 0.1);
    padding-left: 16px;
    padding-right: 16px;
    color: #000;
    font-size: 14px;
    font-family: roboto, Arial, Helvetica, sans-serif;
    outline: none;
}


.dark
.link-frm-con input{
    background-color: rgba(0, 0, 0, 0.3);
    color: #777;
}

.link-frm-con button{
    display: block;
    width: 100%;
    height: 40px;
    margin-top: 16px;
    margin-bottom: 16px;
    box-sizing: border-box;
    background-color: #333;
    border: none;
    border-radius: 8px;
    font-size: 12px;
    font-family: roboto, Arial, Helvetica, sans-serif;
    outline: none;
    color: #FFF;
    font-weight: 600;
    opacity: 1;
    transition: all 0.2s;
    /* box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5); */
}

.link-frm-con button:active{
    opacity: 0.8;
}

.link-frm-con button:disabled{
    opacity: 0.3;
}

.dark
.link-frm-con 
button{
    background-color: var(--primary-light);
    color: var(--primary-dark);
}

.link-frm-con h2,
.link-frm-con h4{
    text-align: center;
}

.link-frm-con ul{
    padding: 24px;
    list-style: decimal;
    margin-top: 0px;
    padding-top: 0px;
}

.link-frm-con li{
    padding: 8px 16px;
    color: #555;
    /* border-bottom: solid 1px rgba(0, 0, 0, 0.1); */
}

.link-frm-con a{
    color: #333;
    display: flex;
    align-items: center;
    height: 40px;
    text-decoration: none;
    background-color: rgba(0, 0, 0, 0.05);
    padding: 0px 16px;
    border-radius: 3px;
    margin-bottom: 1px;
    border-bottom: solid 1px rgba(0, 0, 0, 0.15);
}


.dark
.link-frm-con a{
    border-bottom: solid 1px rgba(255,255,255, 0.08);
}

.link-frm-con a div{
    flex: 1;
    font-weight: 600;
    /* color: rgb(49, 116, 172); */
    color: #333;
    font-size: 12px;
}


.dark
.link-frm-con a div{
    color: var(--primary-light);
}

.sam-v-u{
    border: solid 1px #DDD;
    padding: 16px;
    border-radius: 8px;
    font-size: 12px;
    text-align: center;
}


.dark
.sam-v-u{
    border: solid 1px rgba(255,255,255,0.1);
    color: #777;
}

.dark
.link-frm-con 
h2{
    color: var(--primary-light);
}

.dark
.link-frm-con 
h4{
    color: var(--primary-light);
}



.ap-so-con{
    display: block;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    padding: 16px;
}


.ap-search-con{
    height: 56px;
    /* background-color: red; */
    box-sizing: border-box;
    overflow: hidden;
    display: flex;
    align-items: center;
    position: relative;
    /* padding: 0px 16px; */
    border-radius: 8px;
    border: solid 1px rgba(0, 0, 0, 0.1);
    padding-left: 8px;
    padding-right: 8px;
    border-bottom: solid 2px #DDD;
}

.dark
.ap-search-con{
    border: solid 1px rgba(255,255,255, 0.1);
    border-bottom: solid 2px rgba(255,255,255, 0.1);
}

.ap-s-search-btn{
    position: absolute;
    right: 60px;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 32px;
    width: 32px;
    box-sizing: border-box;
    padding: 0px;
    border-radius: 50%;
    outline: none;
    cursor: pointer;
}



.ap-s-search-btn:hover{
    background-color: rgba(0, 0, 0, 0.1);
}

.ap-s-search-btn:active{
    background-color: rgba(0, 0, 0, 0.1);
}

.ap-search-con input{
    /* width: 100%; */
    flex: 1;
    text-align: left;
    box-sizing: border-box;
    height: 40px;
    border-radius: 6px;
    border: none;
    background-color: rgba(0, 0, 0, 0.1);
    font-size: 14px;
    font-family: roboto, Arial, Helvetica, sans-serif;
    outline: none;
    padding-left: 16px;
    padding-right: 40px;
    
}

.dark 
.ap-search-con 
input{
    background-color: rgba(0, 0, 0, 0.3);
    color: #777;
}

.ap-s-close{
    /* position: absolute;
    right: 4px; */
    background-color: rgba(0, 0, 0, 0);
    /* border: none; */
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    box-sizing: border-box;
    padding: 0px;
    border-radius: 6px;
    outline: none;
    cursor: pointer;
    border: solid 1px rgba(0, 0, 0, 0.1);
}

.ap-s-close:hover{
    background-color: rgba(0, 0, 0, 0.1);
}

.ap-s-close:active{
    background-color: rgba(0, 0, 0, 0.1);
}



.ap-results-con{
    display: block;
    text-align: left;
}

.res-header{
    text-align: center;
    background-color: #555;
    padding: 8px;
    border-radius: 8px 8px 2px 2px;
    color: #FFF;
    margin-bottom: 4px;
    border-bottom: solid 1px rgba(0, 0, 0, 0.1);
    font-size: 16px;
    font-weight: 100;
    font-family: roboto, Arial, Helvetica, sans-serif;
}

.search-item-con{
    display: flex;
    align-items: center;
    flex-direction: row;
    min-height: 40px;
    border: solid 1px rgba(0, 0, 0, 0);
    border-bottom: solid 1px rgba(0, 0, 0, 0.1);
    padding: 16px 16px;
    border-radius: 3px;
    margin-bottom: 1px;
    width: 100%;
    box-sizing: border-box;
    background-color: rgba(0, 0, 0, 0.05);
    text-align: left;
    font-size: 14px;
    cursor: pointer;
}

.dark
.search-item-con{
    border-bottom: solid 1px rgba(255,255,255, 0.08);
}

.search-item-con:hover{
    background-color: rgba(0, 0, 0, 0.1);
}

.search-item-con:active{
    background-color: rgba(0, 0, 0, 0.2);
}

.dark
.search-item-con{
    color: var(--primary-light);
}

.sic-title{
    flex: 1;
    overflow: hidden;
    font-weight: 600;
    font-size: 12px;
}

.sec-title-holder{
    /* border-bottom: solid 2px #333;  */
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-top: 24px;
    /* margin-bottom: 4px; */
    /* border-radius: 8px; */
    /* overflow: hidden; */
    position: relative;
}

.sth-title{
    padding: 8px 16px;
    color: #FFF;
    background-color: #333;
    margin-right: auto;
    border-radius: 8px 8px 0px 0px;
    font-size: 14px;
    font-weight: 600;
    display: flex;
    align-items: center;
    /* margin-bottom: -2px; */
}

.dark
.sth-title{
    color: var(--primary-dark);
    background-color: var(--primary-light);
}

.sth-underline{
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    height: 2px;
    background-color: #333;
    border-radius: 2px;
}

.dark
.sth-underline{
    background-color: var(--primary-light);
}

.sic-hostname{
    font-size: 10px;
    color: rgba(0, 0, 0, 0.5);
    font-weight: 100;
}

.dark
.sic-hostname{
    color: rgba(255,255,255, 0.3);
}




