
.homepage2-con{
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    /* -webkit-overflow-scrolling: touch; */
}

.hp2-head-bg{
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    height: 72px;
    z-index: 998;
}

.hp2-head-bg::before{
    /* display: none; */
    content: ' ';
    position: absolute;
    bottom: -2px;
    height: 16px;
    left: 0px;
    right: 0px;
    background-color: rgba(0,0,0,0.1);
    filter: blur(2px);
}

.hp2-head{
    height: 72px;
    display: flex;
    align-items: center;
    flex-direction: row;
    z-index: 999;
    /* box-shadow: 0px 2px 6px rgba(0,0,0,0.05); */
    box-sizing: border-box;
    position: relative;
    background-color: var(--layout-bg-color);
    
    /* filter: drop-shadow(0px 2px 4px rgba(0,0,0,0.1)); */
}

.light 
.hp2-head{
    /* background-color: #f5f5f5; */
    border-bottom: solid 1px rgba(0,0,0,0.1);
}


.dark 
.hp2-head{
    /* background-color: var(--primary-dark); */
    border-bottom: solid 1px rgba(255,255,255,0.1);
}




.hp2-search-con{
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    z-index: 2;
    background-color: var(--layout-bg-color);
    display: flex;
    align-items: center;
    flex-direction: row;
    transition: all 0.3s;
    /* transform: translateX(100); */
}



.light .hp2-search-con{
    background-color: var(--layout-bg-color);
}


.dark .hp2-search-con{
    background-color: var(--primary-dark);
}

.hp2-in{
    flex: 1;
    /* margin-left: 16px; */
    height: 40px;
    border: none;
    border-radius: 8px;
    font-family: roboto, Arial, Helvetica, sans-serif;
    font-size: 16px;
    
    padding-left: 16px;
    padding-right: 16px;
    outline: none;
}

.light
.hp2-in{
    background-color: rgba(0,0,0,0.1);
}

.dark
.hp2-in{
    background-color: rgba(0,0,0,0.5);
    color: #888;
}

.hp2-h-logo{
    height: 56px;
    margin-left: 16px;
    margin-right: 16px;
    cursor: pointer;
    transition: all 0.5s;
    transform: scale3d(1,1,1);
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
}

.hp2-logo-anim{
    animation: impact 0.5s both;
}

.hp2-h-btn{
    background-color: rgba(0,0,0,0.0);
    border: none;
    border-radius: 8px;
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 8px;
    margin-left: 8px;
    outline: none;
    transition: all 0.2s;
    cursor: pointer;
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
}

.hp2-h-btn:active{
    background-color: rgba(0,0,0,0.1);
}


.hp2-h-btn:disabled{
    opacity: 0.2;
}

.hp2-sl-lbl{
    font-size: 12px;
    color: #555;
    font-weight: 600;
}



.hp2-pg-curr-lbl{
    font-size: 12px;
    border: solid 1px rgba(0,0,0,0.1);
    padding: 4px 8px;
    border-radius: 8px;
    height: 40px;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    margin-left: 4px;
    margin-right: 4px;
    color: #333;
}

.dark
.hp2-pg-curr-lbl{
    border: solid 1px rgba(255,255,255,0.1);
    color: #777;
}

.hp2-h-btn2{
    box-sizing: border-box;
    background-color: rgba(0,0,0,0.0);
    border: solid 1px rgba(0,0,0,0.1);
    /* border: none; */
    border-radius: 8px;
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* margin-right: 8px;
    margin-left: 8px; */
    outline: none;
    transition: all 0.2s;
    cursor: pointer;
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
}

.hp2-h-btn2:active{
    background-color: rgba(0,0,0,0.1);
}

.hp2-h-btn2:disabled{
    opacity: 0.5;
    cursor: not-allowed;
}


.dark
.hp2-h-btn2{
    border: solid 1px rgba(255,255,255,0.1);
}



.hp2-h-btn3{
    box-sizing: border-box;
    background-color: rgba(0,0,0,0.0);
    border: solid 1px rgba(0,0,0,0.1);
    /* border: none; */
    border-radius: 8px;
    height: 40px;
    /* width: 40px; */
    display: flex;
    align-items: center;
    justify-content: center;
    /* margin-right: 8px;
    margin-left: 8px; */
    outline: none;
    transition: all 0.2s;
    padding-right: 12px;
    cursor: pointer;
}

.hp2-h-btn3:active{
    background-color: rgba(0,0,0,0.1);
}

.dark 
.hp2-h-btn3{
    color: #777;
    border: solid 1px rgba(255,255,255,0.1);
}

.hp2-content{
    flex: 1;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 8px;
    /* box-shadow: inset 0px 2px 6px rgba(0,0,0,0.1); */
    /* border-top: solid 1px rgba(0,0,0,0.1); */
}

.light
.hp2-content{
    background-color: var(--layout-bg-color);
}

.dark
.hp2-content{
    background-color: var(--primary-dark);
}


.hp2-item-con{
    min-height: 86px;
    display: flex;
    flex-direction: row;
    align-items: stretch;
    position: relative;
    cursor: pointer;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
    border-bottom: solid 1px var(--border-color);
    padding-top: 8px;
    padding-bottom: 8px;
}

.hp2-item-img-con{
    height: 92px;
    width: 68px;
    overflow: hidden;
    margin-left: 8px;
    position: relative;
    /* border: solid 1px rgba(0,0,0,0.3); */
    box-sizing: border-box;
    /* background-color: #222; */
    border-radius: 4px;
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
    box-shadow: 0px 2px 2px rgba(0,0,0,0.3);
}

.hp2-item-img-con img{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: top;
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
    /* border: solid 1px var(--border-color); */
    
    /* box-sizing: border-box; */
}

.hp2-item-lbl-con{
    flex: 1;
    overflow: hidden;
    text-align: left;
    margin-left: 16px;
    margin-right: 8px;
    /* border-bottom: solid 1px var(--border-color); */
    /* height: 100%; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-right: 40px;
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
    padding-top: 8px;
    padding-bottom: 8px;
    /* margin: auto; */
}

