.hp2-item-lbl-con{
    border-bottom: solid 1px rgba(255,255,255,0.05);
} */

.hp2-item-title{
    font-size: 14px;
    font-weight: 600;
    color: var(--text-color);
    /* padding-top: 8px; */
}

/* .light
.hp2-item-title{
    color: #333;
}

.dark
.hp2-item-title{
    color: var(--primary-light);
} */


.hp2-item-desc{
    font-size: 12px;
    color: #777;
}

.hp2-item-navlink{
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    background-color: rgba(0,0,0,0);
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
    border-radius: 8px;
}

.hp2-item-navlink:active{
    background-color: rgba(0,0,0,0.05);
}

.hp2-item-more{
    position: absolute;
    top: 0px;
    bottom: 0px;
    right: 0px;
    width: 40px;
    height: 40px;
    margin-top: auto;
    margin-bottom: auto;
    background-color: rgba(0,0,0,0);
    border: none;
    outline: none;
    transition: all 0.2s;
    border-radius: 8px;
    z-index: 2;
    cursor: pointer;
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
}

.hp2-item-more:active{
    background-color: rgba(0,0,0,0.1);
}

.hp2-foot{
    height: 56px;
    border-top: solid 1px rgba(0,0,0,0.1);
    z-index: 999;
    box-shadow: 0px -2px 4px rgba(0,0,0,0.1);
}



.hp2-foot-con{
    position: relative;
    height: 56px;
    z-index: 999;
}

.hp2-pg-con-bg{
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    height: 56px;
    z-index: 0;
    /* background-color: #FFF; */
}

.hp2-pg-con-bg::before{
    /* display: none; */
    content: ' ';
    position: absolute;
    top: -1px;
    height: 16px;
    left: 0px;
    right: 0px;
    background-color: rgba(0,0,0,0.1);
    filter: blur(2px);
}


.hp2-pg-con{
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    height: 56px;
    box-sizing: border-box;
    z-index: 99;
    display: flex;
    align-items: center;
    flex-direction: row;
    padding-right: 8px;
    padding-left: 16px;
    /* margin-top: -2px; */

    border-top: solid 1px rgba(0,0,0,0.1);
    z-index: 999;
    background-color: #eee;
    /* box-shadow: 0px -2px 4px rgba(0,0,0,0.1); */
}

.dark
.hp2-pg-con{
    background-color: var(--primary-dark);
    border-top: solid 1px rgba(255,255,255,0.1);
}

.no-story-found{
    font-size: 24px;
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #777;
}

.c-new-tag{
    position: absolute;
    z-index: 2;
    bottom: 8px;
    right: 4px;
    font-size: 8px;
    font-weight: 600;
    background-color: red;
    color: #FFF;
    padding: 2px 4px;
    border-radius: 4px;
    animation: blink 2s both alternate-reverse infinite linear;
    transform: rotate(-25deg);
    letter-spacing: 2px;
}

.lupdate-lbl{
    font-size: 10px;
    margin-top: 8px;
}

.dark .lupdate-lbl{
    color: rgb(165, 155, 97);
}

.light .lupdate-lbl{
    color: rgb(46, 112, 174);
}

@keyframes blink{
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

@keyframes impact{
    0%{
        transform: scale3d(0,0,0);
    }


    25%{
        transform: scale3d(1.3,1.3,1.3);
    }

    50%{
        transform: scale3d(0.8,0.8,0.8);
    }

    100%{
        transform: scale3d(1,1,1);
    }
}